<template>
  <div class="sidebar">
    <el-menu
      class="sidebar-el-menu"
      :default-active="onRoutes"
      :collapse="collapse"
      background-color="#324157"
      text-color="#bfcbd9"
      active-text-color="#20a0ff"
      unique-opened
      router>
      <template v-for="item in items">
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index">
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{ item.title }}</span>
            </template>
            <template v-for="subItem in item.subs">
              <el-submenu v-if="subItem.subs" :index="subItem.index" :key="subItem.index">
                <template slot="title">{{ subItem.title }}</template>
                <el-menu-item
                  v-for="(threeItem,i) in subItem.subs"
                  :key="i"
                  :index="threeItem.index"
                >{{ threeItem.title }}
                </el-menu-item>
              </el-submenu>
              <el-menu-item v-else :index="subItem.index" :key="subItem.index">{{ subItem.title }}</el-menu-item>
            </template>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
    import bus from '../common/bus'

    export default {
        data () {
            return {
                collapse: false,
                items: [
                    {
                        icon: 'el-icon-lx-home',
                        index: 'dashboard',
                        title: '系统首页'
                    },
                    {
                        icon: 'el-icon-lx-people',
                        index: 'user-manager',
                        title: '用户管理',
                        subs: [
                            {
                                index: 'user-list',
                                title: '用户列表'
                            },
                            {
                                index: 'user-log',
                                title: '用户登录日志'
                            },
                            {
                                index: 'user-action-log',
                                title: '用户操作日志'
                            },
                            {
                                index: 'user-comment',
                                title: '评论列表'
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-lx-apps',
                        index: 'video-manager',
                        title: '视频管理',
                        subs: [
                            {
                                index: 'video-list',
                                title: '视频列表'
                            },
                            {
                                index: 'video-upload',
                                title: '上传视频'
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-lx-settings',
                        index: 'comment-manager',
                        title: '站点配置',
                        subs: [
                            {
                                icon: 'el-icon-rank',
                                index: 'config-basic',
                                title: '基本配置'
                            },
                            {
                                index: 'config-sms',
                                title: '短信配置'
                            },
                            {
                                index: 'config-ext',
                                title: '附件配置'
                            },
                            {
                                index: 'config-seo',
                                title: 'SEO配置'
                            },
                            {
                                index: 'config-login',
                                title: '登录设置',
                                subs: [
                                    {
                                        index: 'login-wechat',
                                        title: '微信登录设置'
                                    },
                                    {
                                        index: 'login-qq',
                                        title: 'QQ登录设置'
                                    },
                                    {
                                        index: 'login-weibo',
                                        title: '微博登录设置'
                                    }
                                ]
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-lx-profile',
                        index: 'admin-manager',
                        title: '管理员管理',
                        subs: [
                            {
                                index: 'admin-list',
                                title: '管理员列表'
                            },
                            {
                                index: 'admin-log',
                                title: '管理员日志'
                            }
                        ]
                    }
                ]
            }
        },
        computed: {
            onRoutes () {
                return this.$route.path.replace('/', '')
            }
        },
        created () {
            // 通过 Event Bus 进行组件间通信，来折叠侧边栏
            bus.$on('collapse', msg => {
                this.collapse = msg
                bus.$emit('collapse-content', msg)
            })
        }
    }
</script>

<style scoped>
  .sidebar {
    display: block;
    position: absolute;
    left: 0;
    top: 70px;
    bottom: 0;
    overflow-y: scroll;
  }

  .sidebar::-webkit-scrollbar {
    width: 0;
  }

  .sidebar-el-menu:not(.el-menu--collapse) {
    width: 250px;
  }

  .sidebar > ul {
    height: 100%;
  }
</style>



